@each $color, $value in $theme-colors-light {
    .bg-#{$color}-light {
        background-color: $value !important;
    }
}

@each $color, $value in $grays {
    .bg-gray-#{$color} {
        background-color: $value !important;
    }

    .text-gray-#{$color} {
        color: $value !important;
    }
}
