.border-theme {
    border-width: 0;
    border-image: linear-gradient(to right, $secondary 0%, $secondary 20%, $danger 20%, $danger 40%, $warning 40%, $warning 60%, $info 60%, $info 80%, $primary 80%, $primary 100%) !important;
    border-image-slice: 1 !important;
}

.border-lg { 
    border-top: $border-width * 4 solid !important;
    border-right: $border-width * 4 solid !important;
    border-bottom: $border-width * 4 solid !important;
    border-left: $border-width * 4 solid !important;
}
.border-top-lg     { border-top: $border-width * 4 solid !important; }
.border-right-lg   { border-right: $border-width * 4 solid !important; }
.border-bottom-lg  { border-bottom: $border-width * 4 solid !important; }
.border-left-lg    { border-left: $border-width * 4 solid !important; }