@mixin caret($direction: down) {
  @if $enable-caret {
    &::after {
      font-family: 'feather';
      margin-left: $caret-spacing;
      vertical-align: middle;
      @if $direction == down {
        content: "\e842";
      } @else if $direction == up {
        content: "\e845";
      } @else if $direction == right {
        content: "\e844";
      }
    }

    @if $direction == left {
      &::after {
        display: none;
      }

      &::before {
        font-family: 'feather';
        margin-right: $caret-spacing;
        vertical-align: middle;
        content: "\e843";
      }
    }

    &:empty::after {
      margin-left: 0;
    }
  }
}
