//
// ShuffleKit & Bootstrap variables
//

// Color system

$white:    #fff !default;
$gray-100: #f7f8fc !default;
$gray-200: #edeff7 !default;
$gray-300: #e1e4f0 !default;
$gray-400: #ccd0e0 !default;
$gray-500: #9fa5bf !default;
$gray-600: #697296 !default;
$gray-700: #4a5069 !default;
$gray-800: #2d3247 !default;
$gray-900: #1d202f !default;
$black:    #161720 !default;

$grays: () !default;
$grays: map-merge(
  (
    "100": $gray-100,
    "200": $gray-200,
    "300": $gray-300,
    "400": $gray-400,
    "500": $gray-500,
    "600": $gray-600,
    "700": $gray-700,
    "800": $gray-800,
    "900": $gray-900
  ),
  $grays
);

$blue:    #179de3 !default;
$indigo:  #4c51bf !default;
$purple:  #6f42c1 !default; //
$pink:    #e83e8c !default; //
$red:     #e95350 !default;
$orange:  #ffb854 !default;
$yellow:  #ffc107 !default; //
$green:   #48bb78 !default;
$teal:    #20c997 !default; //
$cyan:    #17a2b8 !default; //

$colors: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$colors: map-merge(
  (
    "blue":       $blue,
    "indigo":     $indigo,
    "purple":     $purple,
    "pink":       $pink,
    "red":        $red,
    "orange":     $orange,
    "yellow":     $yellow,
    "green":      $green,
    "teal":       $teal,
    "cyan":       $cyan,
    "white":      $white,
    "gray":       $gray-600,
    "gray-dark":  $gray-800
  ),
  $colors
);

$primary:           $indigo !default;
$secondary:         $gray-600 !default;
$success:           $green !default;
$info:              $blue !default;
$warning:           $orange !default;
$danger:            $red !default;
$light:             $gray-100 !default;
$dark:              $gray-900 !default;

$primary-light:     #d9dbf0 !default;
$secondary-light:   #e1e0e9 !default;
$success-light:     #deefea !default;
$info-light:        #d1eaf8 !default;
$warning-light:     #fdf1de !default;
$danger-light:      #fadadb !default;
                    
$theme-colors: () !default;
$theme-colors: map-merge(
  (
    "primary":          $primary,
    "secondary":        $secondary,
    "success":          $success,
    "info":             $info,
    "warning":          $warning,
    "danger":           $danger,
    "light":            $light,
    "dark":             $dark,
    "white":            $white
  ),
  $theme-colors
);

$theme-colors-light: () !default;
$theme-colors-light: map-merge(
  (
    "primary":    $primary-light,
    "secondary":  $secondary-light,
    "success":    $success-light,
    "info":       $info-light,
    "warning":    $warning-light,
    "danger":     $danger-light,
  ),
  $theme-colors-light
);

// The yiq lightness value that determines when the lightness of color changes from "dark" to "light". Acceptable values are between 0 and 255.
$yiq-contrasted-threshold:    205 !default;


// Typography

$font-family-sans-serif:      'Inter', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$font-family-monospace:       SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
$font-family-base:            $font-family-sans-serif !default;

$font-weight-normal:          400 !default;
$font-weight-semibold:        500 !default;
$font-weight-bold:            800 !default;

$lead-font-weight:            400 !default;
$headings-font-weight:        500 !default;

$display1-weight:             800 !default;
$display2-weight:             800 !default;
$display3-weight:             800 !default;
$display4-weight:             800 !default;

$font-size-base:              1rem !default;
$font-size-lg:                $font-size-base * 1.25 !default;
$font-size-sm:                $font-size-base * .875 !default;

$h1-font-size:                $font-size-base * 2.25 !default;
$h2-font-size:                $font-size-base * 1.875 !default;
$h3-font-size:                $font-size-base * 1.5 !default;
$h4-font-size:                $font-size-base * 1.25 !default;
$h5-font-size:                $font-size-base * 1.125 !default;
$h6-font-size:                $font-size-base !default;

$display1-size:               $font-size-base * 5.25 !default;
$display2-size:               $font-size-base * 4.5 !default;
$display3-size:               $font-size-base * 3.75 !default;
$display4-size:               $font-size-base * 3 !default;


// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-shadows:                true !default;
$enabne-rounded:                true !default;
$enable-responsive-font-sizes:  false !default;


// Spacing

$spacer: 1rem !default;
$spacers: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$spacers: map-merge(
  (
    0: 0,
    1: ($spacer * .25),
    2: ($spacer * .5),
    3: ($spacer * .75),
    4: $spacer,
    5: ($spacer * 1.25),
    6: ($spacer * 1.5),
    8: ($spacer * 2),
    10: ($spacer * 2.5),
    12: ($spacer * 3),
    16: ($spacer * 4),
    20: ($spacer * 5),
    24: ($spacer * 6),
    32: ($spacer * 8),
    40: ($spacer * 10),
    48: ($spacer * 12),
    56: ($spacer * 14),
    64: ($spacer * 16),
  ),
  $spacers
);

$sections: () !default;
$sections: map-merge(
  (
    0: 0,
    1: ($spacer * 4),
    2: ($spacer * 8),
    3: ($spacer * 12),
    4: ($spacer * 16),
    5: ($spacer * 20),
  ),
  $sections
);

$max-width: () !default;
$max-width: map-merge(
  (
    "full": 100%,
    1: 20rem,
    2: 24rem,
    3: 28rem,
    4: 32rem,
    5: 36rem,
    6: 42rem,
    7: 48rem,
    8: 56rem,
    9: 64rem,
    10: 72rem
  ),
  $max-width
);

// Page colors

$body-bg: $white !default;
$body-color: $gray-900 !default;
$text-muted: $gray-600 !default;


// Components
//
// Define common padding and border radius sizes and more.

$component-active-bg:               $primary !default;

$border-width:                      1px !default;
$border-color:                      $gray-300 !default;

$border-radius:                     0.375rem !default;
$border-radius-sm:                  .25rem !default;
$border-radius-lg:                  .5rem !default;

$box-shadow-sm:                     0 1px 2px 0 rgba(0, 0, 0, 0.05) !default;
$box-shadow:                        0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06) !default;
$box-shadow-lg:                     0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06) !default;

$caret-spacing:                     .4em !default;

// Buttons + Forms
//
// Shared variables that are reassigned to `$input-` and `$btn-` specific variables.

$input-btn-focus-width:             .1rem !default;
$input-btn-focus-color:             rgba($component-active-bg, .1) !default;
$btn-active-box-shadow:             none !default;

$input-btn-padding-y:               0.625rem !default;
$input-btn-padding-x:               1.325rem !default;

$input-btn-padding-y-sm:            .375rem !default;
$input-btn-padding-x-sm:            .75rem !default;

$input-btn-padding-y-lg:            .75rem !default;
$input-btn-padding-x-lg:            1.5rem !default;

$input-color:                       $gray-600 !default;
$input-border-color:                $gray-400 !default;
$input-group-addon-bg:              $gray-100 !default;

// Navbars

$navbar-padding-y:                  $spacer !default;
$navbar-nav-link-padding-x:         1rem !default;

$navbar-light-color:                $gray-600 !default;
$navbar-light-hover-color:          $gray-800 !default;
$navbar-light-active-color:         $gray-800 !default;
$navbar-light-disabled-color:       $gray-500 !default;
$navbar-light-toggler-icon-bg:      url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'><path stroke='#{$navbar-light-color}' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/></svg>") !default;
$navbar-light-toggler-border-color: none !default;
$navbar-dark-toggler-border-color:  none !default;


// Dropdowns

$dropdown-font-size:                $font-size-sm !default;

$dropdown-border-width:             0 !default;
$dropdown-min-width:                12rem !default;
$dropdown-padding-y:                1rem !default;

$dropdown-item-padding-y:           .375rem !default;
$dropdown-item-padding-x:           1.5rem !default;

$dropdown-link-color:               $gray-600 !default;
$dropdown-link-hover-bg:            transparent !default;
$dropdown-link-active-bg:           transparent !default;
$dropdown-link-active-color:        $primary !default;
$dropdown-link-hover-color:         $primary !default;

$dropdown-box-shadow:               0 1px 6px rgba(0, 0, 0, 0.06), 0 2px 8px rgba(0, 0, 0, 0.06) !default;


// Badges

$badge-font-weight:                 $font-weight-semibold !default;
$badge-padding-y:                   .5em !default;
$badge-padding-x:                   .8em !default;
$badge-pill-padding-x:              .825em !default;


// Alerts

$alert-link-font-weight:            $font-weight-semibold !default;
$alert-bg-level:                    0 !default;
$alert-border-level:                0 !default;


// Breadcrumb

$breadcrumb-bg:                     transparent !default;


// Popovers

$popover-font-size:                 $font-size-base !default;
$popover-max-width:                 360px !default;
$popover-border-width:              0 !default;

$popover-body-color:                $gray-700;
$popover-body-padding-y:            1.25rem !default;
$popover-body-padding-x:            1.75rem !default;
$popover-box-shadow:                0 1px 6px rgba(0, 0, 0, 0.06), 0 2px 8px rgba(0, 0, 0, 0.06) !default;


// Tooltips

$tooltip-font-size:                 $font-size-base !default;
$tooltip-max-width:                 260px !default;
$tooltip-bg:                        $gray-900 !default;
$tooltip-opacity:                   1 !default;
$tooltip-padding-y:                 .75rem !default;
$tooltip-padding-x:                 1rem !default;


// Tables

$table-cell-padding:                1.25rem !default;
$table-cell-padding-sm:             .6rem !default;

$table-border-width:                $border-width !default;
$table-border-color:                $gray-200 !default;

$table-accent-bg:                   $gray-200 !default;
$table-dark-bg:                     $gray-800 !default;


// Modals

$modal-backdrop-bg:                 $gray-900 !default;
$modal-backdrop-opacity:            .8 !default;
$modal-content-box-shadow-sm-up:    $box-shadow-lg !default;


// Close button

$close-font-weight:                 400 !default;
$close-color:                       $gray-900 !default;
$close-text-shadow:                 none !default;


// Pagination

$pagination-padding-y:              1rem !default;
$pagination-padding-x:              1.5rem !default;


// Cards

$card-border-radius:                $border-radius-lg !default;
$card-spacer-y:                     2.25rem !default;
$card-spacer-x:                     2.25rem !default;

$card-border-color:                 $border-color !default;
$card-bg:                           $body-bg !default;
$card-cap-bg:                       $body-bg !default;


// Toasts

$toast-padding-x:                   1.25rem !default;
$toast-padding-y:                   .75rem !default;

$toast-border-color:                $border-color !default;
$toast-border-radius:               $border-radius !default;
$toast-box-shadow:                  0 1px 6px rgba(0, 0, 0, 0.06), 0 2px 8px rgba(0, 0, 0, 0.06) !default;

$toast-header-background-color:     rgba($white, .8) !default;
$toast-color:                       $gray-700 !default;

// Carousel

$carousel-control-width:            15% !default;
$carousel-control-font-size:        48px !default;

$carousel-indicator-width:          12px !default;
$carousel-indicator-height:         12px !default;
$carousel-indicator-border-radius:  50% !default;
$carousel-indicator-spacer:         7px !default;
