.pattern {
    position: relative;

    &-attached {
        position: absolute;
        pointer-events: none;
        width: 100%;
        height: 100%;
        bottom: 100%;
        left: 0;
        overflow: hidden;

        svg {
            position: inherit;
            width: 100%;
            height: 100%;
        }

        &-top {
            top: 100% !important;
        }
    }

    &-item {
        position: absolute;
        pointer-events: none;
    }
}

.pattern-v-left, .pattern-v-right {
    position: absolute;
    top: 0;
    height: 100%;

    .pattern-attached {
        bottom: 0;
    }
}

.pattern-v-left {
    left: 0;
}

.pattern-v-right {
    right: 0;
}
