.btn-white {
    color: $primary;
}

@each $color, $value in $theme-colors-light {
    .btn-#{$color}-light {
        background-color: $value;
        color: map-get($theme-colors, $color);
        border-color: transparent !important;

        &:hover {
            color: map-get($theme-colors, $color);
            background-color: darken($value, 4%);
        }
    }
}
