.icon {
    display: inline-block;
    padding-right: 1rem;
    
    svg {
        width: 3rem;
        height: 3rem;
        overflow: hidden;
        vertical-align: middle;
    }

    &.icon-small {
        padding-right: 0.75rem;
        & > svg {
            width: 2rem;
            height: 2rem;
        }
    }
}
