@each $color, $value in $theme-colors {
    .alert-#{$color} {
        @include alert-variant(theme-color-level($color, $alert-bg-level), theme-color-level($color, $alert-border-level), color-yiq(theme-color($color)));
    }
}

@each $color, $value in $theme-colors-light {
    .alert-#{$color}-light {
        background-color: $value;
        color: map-get($theme-colors, $color);

        .alert-link {
            color: darken(map-get($theme-colors, $color), 10%);
        }
    }
}
