[class*=text-underline-] {
    background: repeat-x left 0.9em/0.9em .25em;
}

@each $color, $value in $theme-colors {
    .text-underline-#{$color} {
        background-image: -webkit-gradient(linear,left top,right top,from( $value));
        background-image: -o-linear-gradient(left, $value);
        background-image: linear-gradient(to right, $value)
    }
}

@each $color, $value in $theme-colors-light {
    .text-underline-#{$color}-light {
        background-image: -webkit-gradient(linear,left top,right top,from( $value));
        background-image: -o-linear-gradient(left, $value);
        background-image: linear-gradient(to right, $value)
    }
}

@each $color, $value in $theme-colors-light {
    .text-#{$color}-light {
        color: $value !important;
    }
}

.font-size-base { font-size: $font-size-base !important; }
.font-size-lg { font-size: $font-size-lg !important; }
.font-size-sm { font-size: $font-size-sm !important; }

.font-weight-semibold { font-weight: $font-weight-semibold !important; }